import Vue from "vue";
import VueRouter from "vue-router";
import homeView from "../views/home-view.vue";

Vue.use(VueRouter);

const routes = [
	{
		path: "/",
		name: "home",
		component: homeView,
	},
];

const router = new VueRouter({
	mode: "history",
	base: process.env.BASE_URL,
	routes,
});

export default router;
