<style>
* {
  font-family: monospace;
}
:root {
  --console-height: 70vh;
  --console-width: min(95%, 1200px);
}
.info-bar {
  position: absolute;
  top: calc(
    var(--console-height) + calc(calc(100vh - var(--console-height)) / 2)
  );
  width: calc(var(--console-width) - 2px) !important;
  transform: translateY(-100%);
}
.console {
  width: var(--console-width);
  height: var(--console-height);
  max-height: var(--console-height);
  margin: auto;
  overflow: scroll;
  /* border: white solid 2px; */
}
::-webkit-scrollbar {
  width: 0px;
