<template>
  <div class="wrapper">
    <span class="info-page">Manual page jan(1)</span>
    <span class="info-progress">line {{ scrollPercentage.toFixed(0) }}</span>
    <!-- <span class="info-controls">press h for help </span> -->
  </div>
</template>

<script>
export default {
  name: "info-bar",
  data() {
    return {
      scrollPercentage: 0,
    };
  },
  computed: {
    console() {
      return this.$parent.$parent.$refs.console;
    },
  },
  mounted() {
    this.console.addEventListener("scroll", this.calculateScrollPercentage);
  },
  methods: {
    calculateScrollPercentage() {
      this.scrollPercentage =
        (this.console.scrollTop /
          (this.console.scrollHeight - this.console.clientHeight)) *
        100;
    },
  },
};
</script>

<style scoped>
.wrapper {
  background-color: white;
  width: 100%;
  color: black;
  display: flex;
  gap: 10px;
  align-items: center;
  padding-inline: 10px;
}
</style>
